	module.exports = {"pathContext":{"docHtml":"<h3>Note</h3>\n<p>As of 2.0.0, the History.md file has been deprecated. <a href=\"https://github.com/chaijs/chai-http/commits/master\">Please refer to the full\ncommit logs available on GitHub</a>.</p>\n<hr>\n<h1>1.0.0 / 2014-10-08</h1>\n<ul>\n<li>update readme</li>\n<li>Merge pull request #14 from keithamus/fix-master</li>\n<li>Fix #13: Convert unroutable addresses to localhost</li>\n<li>Fix #13: Add proper detection of Promise capabilities in tests</li>\n<li>Merge pull request #12 from keithamus/patch-1</li>\n<li>add travis badge</li>\n<li>add travis support</li>\n<li>Fix typo</li>\n<li>Merge pull request #11 from keithamus/refactor-agent</li>\n<li>(chore) Regenerate README.md</li>\n<li>Add list of contributors to package.json</li>\n<li>Add simple usage docs for request api</li>\n<li>Fix typo in readme</li>\n<li>Add cookie assertions</li>\n<li>Add URL query string parameter assertions</li>\n<li>Add request.agent() which allows persisting of cookies</li>\n<li>Add Promises support</li>\n<li>Listen on random port in tests - not 4000 which may be taken</li>\n<li>Add Test class to exports, to enable extending</li>\n<li>Drastically simplify chai.request() by inheriting superagent</li>\n<li>Update SuperAgent to v0.19.x</li>\n<li>Drop harbor and pauli deps.</li>\n</ul>\n<h1>0.5.0 / 2014-08-19</h1>\n<ul>\n<li>Merge pull request #9 from hurrymaplelad/assert_redirect</li>\n<li>Add redirect assertions</li>\n<li>Merge pull request #8 from lxanders/improveReadme</li>\n<li>Fixed missing word and improved wording in documentation</li>\n</ul>\n<h1>0.4.0 / 2013-06-25</h1>\n<ul>\n<li>support: [readme] cleanup</li>\n<li>Merge pull request #7 from pezra/newer-superagent</li>\n<li>remove previous additions to the history</li>\n<li>bump superagent version dependency</li>\n<li>Merge pull request #5 from wookiehangover/patch-1</li>\n<li>Fixing typo in Readme</li>\n<li>readme typo</li>\n</ul>\n<h1>0.3.0 / 2012-10-24</h1>\n<ul>\n<li>minor tweak to http docs</li>\n<li>Readme auto generation support</li>\n<li>readme preview</li>\n<li>improve readme code comments</li>\n<li>add readme</li>\n<li>documentation for request api</li>\n<li>update http assertion docs</li>\n<li>add request test for existing url</li>\n<li>support and tests for header existence</li>\n<li>improve type detection error message</li>\n<li>add support for superagent based testing</li>\n<li>code cleanup</li>\n<li>convert to tdd</li>\n<li>clean up test runner</li>\n</ul>\n<h1>0.2.0 / 2012-05-15</h1>\n<ul>\n<li>chai 1.0.0 compatibility</li>\n<li>update package.json</li>\n</ul>\n<h1>0.1.0 / 2012-03-10</h1>\n<ul>\n<li>Merge branch 'refs/heads/dev'</li>\n<li>git/npm ignore</li>\n<li>Add <code>Asssertion#ip</code>.</li>\n<li>Docs.</li>\n<li>Add test for Assertion#headers.</li>\n<li>Add test for Assertion#json, Assertion#html Assertion#text.</li>\n<li>Add test for Assertion#header.</li>\n<li>Add test for Assertion#status.</li>\n</ul>\n<h1>0.0.1 / 2012-03-06</h1>\n<ul>\n<li>prepping for release 0.0.1</li>\n<li>first lib commit</li>\n<li>initial commit</li>\n</ul>","docPath":"argo-ci/node_modules/chai-http/history","proj":"argo-ci"}}

